<template>
  <div class="top">
     <div>
      <!--<i :class="className" style="font-size: 0.2rem;margin-right: 0.1rem"></i>-->
      <i class="iconfont icon-tishi"></i>
      <span>{{msg}}</span>
      </div>

    <!--<p>{{msg}}</p>-->
  </div>
</template>
<script>
  export default{
    data(){
      return{
        className:'',
        name:'',
        msg:''
      }
    },
    mounted(){
      let name=this.$route.name;
      switch(name){
        case 'serve':
            this.className='iconfont icon-pricetag';
            this.name='服务项目';
            this.msg='小程序发布后，更新商品将实时同步，无需再次发布。';
            break;
        case 'serveInfo':
          this.className='iconfont icon-xinxi';
          this.name='基本信息';
          break;
        case 'pay':
          this.className='iconfont icon-b';
          this.name='微信支付';
          this.msg='微信小程序仅支持微信支付（商户号），且已完成微信认证。个人用户暂不能开通支付功能。';
          break;
        case 'serveSorts':
          this.className='iconfont icon-wenjianjia';
          this.name='服务分类';
          this.msg='添加新的服务分类，为服务分类排序，删除服务分类。你可以在编辑服务时添加服务分类。';
          break;
        case 'set':
          this.className='iconfont icon-yuyue';
          this.name='预约设置';
          this.msg='修改设置，将运用于所有服务的预定页面。';
          break;
        case 'staff':
          this.className='iconfont icon-zhengjian';
          this.name='服务人员';
          this.msg='管理服务人员，顾客可指定预约';
          break;
      }
    }
  }
</script>
<style scoped>
  @import url("../../../static/base.css");
</style>



